@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

/* ScrollBar */

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: #2d424f;
  border-radius: 10px;
}

/* Match Result fade animation */

.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 350ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 350ms;
}

/* Search box animation */

@-webkit-keyframes widthreduce {
  0% {
    width: 65%;
  }
  100% {
    width: 60%;
  }
}

@keyframes widthreduce {
  0% {
    width: 65%;
  }
  100% {
    width: 60%;
  }
}

@-webkit-keyframes withenlarge {
  0% {
    width: 60%;
  }
  100% {
    width: 65%;
  }
}

@keyframes withenlarge {
  0% {
    width: 60%;
  }
  100% {
    width: 65%;
  }
}

.searchenlarge {
  -webkit-animation: withenlarge 0.5s ease-out forwards;
          animation: withenlarge 0.5s ease-out forwards;
}

.searchreduce {
  -webkit-animation: widthreduce 0.5s ease-out;
          animation: widthreduce 0.5s ease-out;
}

